<template>
    <div class="food-detail">
        <Navbar />   

        <div class="container">
            <div class="row">
                <div class="col">
                    <nav aria-label="breadcrumb">
                        <ol class="breadcrumb">
                            <li class="breadcrumb-item"><router-link class="text-muted" to="/">Home</router-link></li>
                            <li class="breadcrumb-item"><router-link class="text-muted" to="/menu">Menu</router-link></li>
                            <li class="breadcrumb-item active" aria-current="page">Food Detail</li>
                        </ol>
                    </nav>
                </div>
            </div>

            <div class="row">
                <div class="col-12 col-lg-6">
                    <img :src="'../assets/img/'+product.gambar" width="100%" :alt="product.nama">
                </div>
                <div class="col-12 col-lg-6 float-left text-left">
                    <h2 class="mt-3 mt-lg-0"><strong>{{product.nama}}</strong></h2>
                    <hr>
                    <h4>Harga : <strong>Rp.{{product.harga}}</strong></h4>
                    <form action="" v-on:submit.prevent>
                        <div class="form-group">
                            <label for="jumlah">Jumlah Pesan</label>
                            <input v-model="pesan.jumlah_pemesanan" type="number" name="jumlah" id="jumlah" class="form-control">
                        </div>
                        <div class="form-group">
                            <label for="jumlah">Keterangan</label>
                            <textarea v-model="pesan.keterangan" name="keterangan" id="keterangan" placeholder="Keterangan seperti : pedas, gak pake bawang.." class="form-control"></textarea>
                        </div>
                        <div class="form-group">
                            <button @click="pemesanan" type="submit" class="btn btn-lg btn-success">Checkout</button>
                        </div>
                    </form>
                </div>
            </div>    
        </div>  

    </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue"
import axios from "axios"

export default {
    name: "FoodDetail",
    components: {
        Navbar,
    },
    data(){
        return{
            product: {},
            pesan: {}
        }
    },
    methods:{
        setProducts(data){
            this.product = data
        },
        pemesanan(){
            if (this.pesan.jumlah_pemesanan){
            this.pesan.products = this.product;
            axios
            .post("http://192.168.100.5:3000/keranjang", this.pesan)
            .then(() => {
                this.$toast.success('Pesanan telah ditambahkan ke keranjang. !', {
                // optional options Object
                type: "success",
                position: "top-right",
                duration: 3000,
                dismissible: true,
                });
            })
            .catch((err) => console.log(err));                
            }else{
                this.$toast.error('Jumlah Pesanan Harus Diisi. !', {
                // optional options Object
                type: "error",
                position: "top-right",
                duration: 4000,
                dismissible: true,
                });
            }
        },
    },
    mounted(){
        axios 
        .get("http://192.168.100.5:3000/product/"+this.$route.params.id)
        .then((response) => this.setProducts(response.data))
        .catch((error) => console.log(error))
    }
}
</script>