<template>
  <div class="home">
    <Navbar />
    <Hero />

    <div class="container">
      <div class="row justify-content-center my-3 my-lg-3">
        <div class="col">
          <h2 class="float-lg-right">Best <strong>food</strong></h2>
        </div>
        <div class="col">
          <router-link class="btn btn-success float-lg-left" to="/menu"><b-icon-eye></b-icon-eye> Lihat Semua</router-link>
        </div>
      </div>

      <div class="row mb-3 justify-content-center">
        <div class="col-md-6 col-lg-3 my-3 my-lg-3 " v-for="product in products" :key="product.id">
          <CardProduct :product="product" />
        </div>
      </div>
    </div>

  </div>
</template>

<script>
// @ is an alias to /src
import Navbar from '@/components/Navbar.vue'
import Hero from '@/components/Hero.vue'
import CardProduct from '@/components/CardProduct.vue'
import axios from 'axios'

export default {
  name: 'Home',
  components: {
    Navbar, Hero, CardProduct
  },
  data(){
    return{
      products: []
    }
  },
  methods:{
    setProducts(data){
      this.products = data
    }
  },
  mounted(){
    axios
      .get("http://192.168.100.5:3000/best-products")
      .then((response) => this.setProducts(response.data))
      .catch((error) => console.log(error))
  }
}
</script>
