<template>
    <div>
      <Navbar />

      <div class="container">

          <div class="row">
              <div class="col">
                  <nav aria-label="breadcrumb">
                      <ol class="breadcrumb">
                          <li class="breadcrumb-item"><router-link class="text-muted" to="/">Home</router-link></li>
                          <li class="breadcrumb-item active" aria-current="page">Menu</li>
                      </ol>
                  </nav>
              </div>
          </div>

        <div class="row">
          <div class="input-group mb-3 mx-3">
            <input v-model="search" @keyup="searchFood" type="text" class="form-control" placeholder="Cari Makanan.." aria-label="Username" aria-describedby="basic-addon1">
            <div class="input-group-prepend">
              <span class="input-group-text" id="basic-addon1"><b-icon-search></b-icon-search></span>
            </div>
          </div>          
        </div>
      </div>

        <div class="container">
          <div class="row">
            <div class="col-md-6 col-lg-4 my-3" v-for="product in products" :key="product.id">
              <CardProduct :product="product" />
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue'
import CardProduct from '@/components/CardProduct.vue'
import axios from 'axios'

export default {
  name: 'Menu',
  components: {
    Navbar, CardProduct
  },
  data(){
    return{
      products: [],
      search: ''
    }
  },
  methods:{
    setProducts(data){
      this.products = data
    },
    searchFood(){
      axios
        .get("http://192.168.100.5:3000/product?q="+this.search)
        .then((response) => this.setProducts(response.data))
        .catch((error) => console.log(error))      
    }
  },
  mounted(){
    axios
      .get("http://192.168.100.5:3000/product")
      .then((response) => this.setProducts(response.data))
      .catch((error) => console.log(error))
  }    
}
</script>

