<template>
  <div class="keranjang">
      <Navbar />
      <div class="container">
            <div class="row">
                <div class="col">
                    <nav aria-label="breadcrumb">
                        <ol class="breadcrumb">
                            <li class="breadcrumb-item"><router-link class="text-muted" to="/">Home</router-link></li>
                            <li class="breadcrumb-item active" aria-current="page">Keranjang</li>
                        </ol>
                    </nav>
                </div>
            </div>

            <div class="row">
                <div class="col">
                    <div class="table-responsive mt-4">
                        <table class="table">
                        <thead>
                            <tr>
                            <th scope="col">#</th>
                            <th scope="col">Foto</th>
                            <th scope="col">Makanan</th>
                            <th scope="col">Keterangan</th>
                            <th scope="col">Jumlah</th>
                            <th scope="col">Harga</th>
                            <th nowrap scope="col">Total Harga</th>
                            <th scope="col">Hapus</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(chart, index) in keranjang" :key="chart.id">
                                <th scope="row">{{index+1}}</th>
                                <td><img class="img-fluid shadow" width="100" :src=" '../assets/img/' + chart.products.gambar" :alt="chart.products.nama"></td>
                                <td>{{chart.products.nama}}</td>
                                <td>{{chart.keterangan}}</td>
                                <td>{{chart.jumlah_pemesanan}}</td>
                                <td>Rp. {{chart.products.harga}}</td>
                                <td><strong>Rp. {{chart.products.harga*chart.jumlah_pemesanan}}</strong></td>
                                <td><b-icon-trash @click="hapusKeranjang(chart.id)"></b-icon-trash></td>
                            </tr>
                            <tr>
                                <td colspan="6">
                                    <strong>Total Harga</strong>
                                </td>
                                <td>
                                    <strong>
                                        Rp.{{totalHarga}}
                                    </strong>
                                </td>
                            </tr>
                        </tbody>
                        </table>
                    </div>
                </div>
            </div>


      </div>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import axios from "axios";

export default {
    name: "Keranjang",
    components: {
        Navbar,
    },
    data(){
        return{
            keranjang: []
        }
    },
    methods: {
        setKeranjang(data){
            this.keranjang = data;
        },
        hapusKeranjang(id){
            axios
            .delete("http://192.168.100.5:3000/keranjang/"+id)
            .then(() => {
                this.$toast.success('Pesanan telah dihapus dari keranjang. !', {
                type: "success",
                position: "top-right",
                duration: 4000,
                dismissible: true
            });
            //update data keranjang setelah hapus
            axios
            .get("http://192.168.100.5:3000/keranjang")
            .then((response) => this.setKeranjang(response.data))
            .catch((error) => console.log(error))
          })
            .catch((error) => console.log(error));
        }
    },
    mounted(){
        axios
        .get("http://192.168.100.5:3000/keranjang")
        .then((response) => this.setKeranjang(response.data))
        .catch((error) => console.log(error))
    },
    computed: {
        totalHarga(){
            return this.keranjang.reduce(function(items, data) {
                return items+(data.products.harga*data.jumlah_pemesanan)
            }, 0)
        }
    }
};
</script>

<style>
    h2.keranjang-title{
        text-align: left;
    }
</style>